import React from "react";
import { data, editPost, domReady } from "@frontkom/gutenberg-js";

// Gutenberg JS Style
import "@frontkom/gutenberg-js/build/css/block-library/style.css";
import "@frontkom/gutenberg-js/build/css/style.css";

function delay(t, v) {
  return new Promise(function (resolve) {
    setTimeout(resolve.bind(null, v), t);
  });
}

class Editor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loader: true,
    };
  }

  componentDidMount() {
    delay(7000)
      .then(() => {
        this.setState({
          loader: false,
        });
      })
      .then(() => {
        const settings = {
          alignWide: true,
          availableTemplates: [],
          allowedBlockTypes: true,
          disableCustomColors: false,
          disablePostFormats: false,
          titlePlaceholder: "Add title",
          bodyPlaceholder: "Insert your custom block",
          isRTL: false,
          autosaveInterval: 0,
          postLock: {
            isLocked: false,
          },
          canPublish: false,
          canSave: false,
          canAutosave: true,
          mediaLibrary: true,
        };

        // reset localStorage
        //localStorage.removeItem("g-editor-page");

        // Disable tips
        data.dispatch("core/nux").disableTips();
        // Initialize the editor
        window._wpLoadGutenbergEditor = new Promise(function (resolve) {
          domReady(function () {
            resolve(
              editPost.initializeEditor("editor", "page", 1, settings, {})
            );
          });
        });
      });
  }

  render() {
    const { loader } = this.state;
    if (loader) {
      return (
        <div className="row">
          <div className="col s12 center">
            <div className="preloader-wrapper big active">
              <div className="spinner-layer spinner-blue-only">
                <div className="circle-clipper left">
                  <div className="circle" />
                </div>
                <div className="gap-patch">
                  <div className="circle" />
                </div>
                <div className="circle-clipper right">
                  <div className="circle" />
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return <div id="editor" className="gutenberg__editor"></div>;
    }
  }
}

export default Editor;
