export const getMedias = async function () {
  return await fetch(BASEURL + "api/v1/files/filter_files", {
    method: "POST",
    headers: {
      Accept: "application/json",
    },
    body: new URLSearchParams(
      "filter_name=file_type&filter_value%5B%5D=jpg&filter_value%5B%5D=png&filter_value%5B%5D=gif"
    ),
  })
    .then((response) => response.json())
    .then((response) => {
      if (response.code == 200) {
        return response.data.map((file, index) => {
          return {
            ...file,
            id: index + 1,
            caption: { raw: "", rendered: "" },
            date_gmt: date,
            date,
            link: `${BASEURL}/${file.file_path}${file.file_name}.${file.file_type}`,
            media_type: "image",
            mime_type: "image/jpeg",
            source_url: `${BASEURL}/${file.file_path}${file.file_name}.${file.file_type}`,
            media_details: {
              file: "",
              height: 2100,
              image_meta: {},
              sizes: {},
              width: 3360,
            },
            title: { raw: "", rendered: "" },
          };
        });
      }
    })
    .catch((err) => {
      console.error(err);
    });
};

export const uploadFile = async function (file) {
  /* chunkCount: 1
  chunkIndex: 0
  chunkSize: 2097152
  chunkSizeStart: 0
  fileBlob: (binary)
  fileId: 115175_PVuKqgJQ_400x400.png
  fileName: PVuKqgJQ_400x400.png
  fileRelativePath: PVuKqgJQ_400x400.png
  fileSize: 115175
  retryCount: 0
  initialPreview: []
  initialPreviewConfig: []
  initialPreviewThumbTags: []
  uploadToken: SOME-TOKEN
  curDir: ./uploads/ */

  let fileInput = document.querySelector(
    ".editor-block-list__block.is-selected input[type=file]"
  );
  var files = fileInput.files;
  if (files.length == 0) {
    return false;
  }

  file = files[0];

  var formData = new FormData();
  formData.append("chunkCount", "1");
  formData.append("chunkIndex", "0");
  formData.append("chunkSize", file.size);
  formData.append("chunkSizeStart", 0);
  formData.append("fileBlob", file);
  formData.append("fileId", file.name);
  formData.append("fileName", file.name);
  formData.append("fileRelativePath", `./uploads/${file.name}`);
  formData.append("fileSize", file.size);
  formData.append("retryCount", 0);
  formData.append("initialPreview", []);
  formData.append("initialPreviewConfig", []);
  formData.append("initialPreviewThumbTags", []);
  formData.append("uploadToken", "SOME-TOKEN");
  formData.append("curDir", "./uploads/");

  return await fetch(BASEURL + "admin/archivos/ajax_upload_file", {
    method: "POST",
    headers: {
      Accept: "application/json",
    },
    body: formData,
  })
    .then((response) => response.json())
    .then((response) => {
      return ({
        id: 1,
        guid: {},
        date_gmt: {},
        date: {},
        modified: "",
        link: `${BASEURL}${response.post.fileRelativePath}`,
        title: "",
        author: "",
        generated_slug: "",
        permalink_template: "",
        type: "",
        status: "",
        slug: "",
        modified_gmt: "",
        mime_type: "",
        media_details: {},
        description: {},
        caption: {},
        alt_text: {},
        template: "",
        meta: {},
        ping_status: "",
        comment_status: "",
        post: 1,
        source_url: `${BASEURL}${response.post.fileRelativePath}`,
        missing_image_sizes: [],
      });
    })
    .catch((err) => {
      console.error(err);
    });
};

